<template>
<div class="BlurModal_page">
    <div class="BlurModal_page_block">
        <div>
            <p class="modal_title">Новое рациональное решение</p>
            <p class="modal_header_text">Привет! Давайте определим цели и <br> подберем название для вашего проекта, <br> чтобы он получился крутым! :)</p>
        </div>
        <div>
            <div class="targget_block">
                <input v-model="targget_input"
                class="reg_input"
                placeholder="Выберите цели из списка"
                type="text"
                @focus="targget_block_listShow = true">
                <img @click="targget_block_listShow = !targget_block_listShow"
                :class="{trans: targget_block_listShow == true}"
                src="./../../img/expand_more.svg" alt="">
                <div v-show="targget_block_listShow == true"
                 class="targget_block_list">
                    <div class="targget_block_list_element"
                    v-for="t in filteredList()"
                    :key="t"
                    @click="(targgetBlockChoice(t)) || (targget_block_listShow = !targget_block_listShow)">
                        <p>{{t.title}}</p>
                    </div>
                </div>
            </div>
            <div class="targget_block_choice_block">
                <div class="targget_block_choice"
                v-for="t in targget_block_choice"
                :key="t" >
                <p>{{t.title}}</p>
                <img src="./../../img/close.svg" alt="">
                </div>
            </div>
            <div class="name_block">
                <input v-model="name_input" class="reg_input name_input"
                placeholder="Введите название"
                type="text">
                <img src="./../../img/generate.svg" alt="">
            </div>
        </div>
        <div class="BlurModal_page_btn">
            <div class="reg_btn BlurModal_btn" @click="uotModal">Отмена</div>
            <div v-if="(targget_block_choice.length == 0) || (name_input == '') "
             class="reg_btn BlurModal_btn NoActiv">Продолжить</div>
            <div v-if="(targget_block_choice.length != 0) &&  (name_input != '')"
             @click="ChoseСreate()"
             class="reg_btn BlurModal_btn">Продолжить</div>
        </div>
    </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            name_input: 'Валера прекрасен',
            targget_input: '',
            targget_block_list: [
                {
                    title: 'РК',
                    id: '123',
                },
                {
                    title: 'HZ',
                    id: '123',
                },
                {
                    title: 'HR',
                    id: '123',
                },
                {
                    title: 'Стажировка',
                    id: '123',
                },
                {
                    title: 'Отношения',
                    id: '123',
                },
                {
                    title: 'Психология',
                    id: '123',
                },
                {
                    title: 'Рациональность',
                    id: '123',
                },
                {
                    title: 'Турнир',
                    id: '123',
                },
            ],
            targget_block_choice: [],
            targget_block_listShow: false,
            title: '',
            id: ''
        }
    },
    methods: {
        uotModal() {
            this.$emit("modal", false)
        },
        filteredList() {
            return this.targget_block_list.filter(targget_block_list => targget_block_list.title.toLowerCase().includes(this.targget_input.toLowerCase()))
        },
        targgetBlockChoice(t) {
            const newRequest = {
                title: t.title,
                id: t.id,
            };
            this.targget_block_choice.push(newRequest);
        },
        rout(r) {
            this.$router.push(r);
        },
        ChoseСreate() {
            this.$store.state.targget_block_choice = this.targget_block_choice;
            this.$store.state.name_input = this.name_input;
            this.$router.push('/AddObject')
        }

    }
}
</script>

<style>
.BlurModal_page {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.BlurModal_page_block {
    background: rgba(255, 255, 255, 0.8);
    box-shadow: -4px -4px 8px 2px rgba(0, 0, 0, 0.05), 16px 16px 24px -12px rgba(0, 0, 0, 0.16);
    backdrop-filter: blur(8px);
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 32px;
}
.name_block {
    display: flex;
    gap: 24px;
    align-items: center;

}
.targget_block {
    position: relative;
}
.targget_block img {
    position: absolute;
    top: 13px;
    right: 13px;
    transition: 0.2s;
}
.name_input {
    margin: 0;
}
.BlurModal_page_btn {
    display: flex;
    flex-wrap: nowrap;
    gap: 24px;
    justify-content: center;
}
.BlurModal_btn {
    width: 100%;
}
.targget_block_list {
    width: 95.5%;
    background: #FFFFFF;
    border: 1px solid #111111;
    box-shadow: -4px -4px 8px 2px rgba(0, 0, 0, 0.05), 16px 16px 24px -12px rgba(0, 0, 0, 0.16);
    backdrop-filter: blur(8px);
    border-radius: 2px;
    position: absolute;
    top: 16px + 48px;
    max-height: 256px;
    overflow-y: scroll;
    padding: 8px;
}
.targget_block_list::-webkit-scrollbar {
  width: 4px;
}
.targget_block_list::-webkit-scrollbar-thumb {
  background-color: #90909083;
  border-radius: 3px;
}
.targget_block_list_element {
    border-bottom: 1px solid #909090;
    padding: 8px;
}
.trans {
    transform: rotate(-0.5turn);
    transition: 0.2s;
}
.targget_block_choice_block {
    display: flex;
    gap: 8px;
    flex-wrap: wrap;
    margin-bottom: 24px;
}
.targget_block_choice {
    display: flex;
    align-items: flex-start;
    padding: 4px 8px;
    gap: 4px;
    width: min-content;
    height: 32px;
    background: #FAF7FF;
    border: 1px solid #8963BA;
    border-radius: 6px;
    align-items: center;
    
}
</style>